import React, {Component} from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { fade } from '@material-ui/core/styles/colorManipulator';
import { withStyles } from '@material-ui/core/styles';
//import SearchIcon from '@material-ui/icons/Search';
import Avatar from '@material-ui/core/Avatar';
//import { AutoComplete } from './index'
import strings from '../strings'

const styles = theme => ({
  root: {
    width: '100%',
  },
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing.unit,
      width: 'auto',
    },
  },
  searchIcon: {
    width: theme.spacing.unit * 9,
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
    width: '100%',
  },
  inputInput: {
    paddingTop: theme.spacing.unit,
    paddingRight: theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
    paddingLeft: theme.spacing.unit * 10,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: 120,
      '&:focus': {
        width: 350,
      },
    },
  },
});

class SearchAppBar extends Component {
  handleChange = name => (event, { newValue }) => {
    this.props.onChange(event, { newValue });
  };

  render() {
    const { classes, /*suggestions*/ } = this.props;

    return (
      <div className={classes.root}>
        <AppBar position="static">
          <Toolbar>
            <IconButton href="#" className={classes.menuButton} color="inherit" aria-label={strings.app_name}>
              <Avatar alt={strings.app_name} src="../favicon.ico" className={classes.avatar} />
            </IconButton>
            <Typography className={classes.title} variant="h6" color="inherit" noWrap>{strings.app_name}</Typography>
            <div className={classes.grow} />
            {/*<div className={classes.search}>
              <div className={classes.searchIcon}>
                <SearchIcon />
              </div>
              <AutoComplete placeholder="Ort suchen …" suggestions={suggestions} onChange={this.handleChange("single")} classes={{ root: classes.inputRoot, input: classes.inputInput }} />
            </div>*/}
          </Toolbar>
        </AppBar>
      </div>
    );
  }
}

SearchAppBar.propTypes = {
  classes: PropTypes.object.isRequired,
  suggestions: PropTypes.array.isRequired,
  onChange: PropTypes.func,
};

export default withStyles(styles)(SearchAppBar);
